.backdrop {
  visibility: visible;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  overflow: scroll;

  opacity: 1;
  transition: opacity 250ms $transition-timing-function,
    visibility 250ms $transition-timing-function;

  &.is-hidden {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
  }
}

.modal-box {
    position: absolute;
    padding: 15px;
    outline: 1px solid $accent-color;
    background-color: $title-color;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    width: 320px;
    top: 0;
    left: 50%;

   transform: translate(-50%, 2%) scale(1);
   transition: transform 300ms $transition-timing-function;

  @media screen and (min-width: $tablet) {
    width: 450px;
    height: 609px;
    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%) scale(1);

    transition: transform 300ms $transition-timing-function;
  }

  @media screen and (min-width: $desktop) {
    width: 528px;
    height: 581px;
  }

  .backdrop.is-hidden & {
    transform: translate(-50%, -50%) scale(0);
  }
}

.modal-box__btn{
    fill: $primary-text-color;
    position: absolute;
    top: 10px;
    right: 10px;
    border-radius: 50%;
    border: 1px solid #ccc;

    display: flex;
    justify-content: center;
    align-items: center;

    margin-left: auto;
    width: 25px;
    height: 25px;

    transition: fill 300ms $transition-timing-function, border-color 300ms $transition-timing-function;

    &:hover,
    &:focus{
        fill: $accent-color;
        border-color: $accent-color;
    }
}

.grid{

    @media screen and(min-width: $tablet){
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    }
 
}

.grid-img{
    outline: 1px solid $accent-color;
    height: 400px;
    margin-bottom: 10px;

    @media screen and(min-width: $tablet){
    height: 150px;
    margin-bottom: 0;
    }

    &:last-child{
        @media screen and(max-width: 767px){
                margin-bottom: 0;
        }
    }
}

.modal-title{
    margin-top: 30px;
    margin-bottom: 20px;
}